.flat-pagination {
  display: none;
  text-align: center;
}
.flat-pagination .swiper-pagination-bullet {
  position: relative;
  overflow: hidden;
  margin: 0 !important;
  border-radius: 0.25rem;
  width: 1.875rem;
  height: 0.1875rem;
  background-color: none;
  opacity: 1;
}
.flat-pagination .swiper-pagination-bullet::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--gray500-color) !important;
  opacity: 1;
}
.flat-pagination .swiper-pagination-bullet::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  display: inline-block;
  width: 0;
  height: 100%;
  background-color: transparent !important;
  opacity: 0;
  transform: translateX(-50%);
  transition: width 0.2s ease-in-out;
}
.flat-pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 0.75rem !important;
}
.flat-pagination .swiper-pagination-bullet-active::after {
  width: 100%;
  background-color: var(--gray900-color) !important;
  opacity: 1;
}
@media (max-width: 47.9375em) {
  .flat-pagination {
    display: block;
    pointer-events: auto;
  }
}

